import { useMutation, useQuery, useQueryClient } from "@tanstack/react-query";
import { CreateWorkflowDTO } from "@everlast/types";
import { createWorkflow, listWorkflows } from "@/lib/mock-api";

const DEFAULT_TENANT = "tenant-demo-1";

export function useWorkflows(tenantId: string = DEFAULT_TENANT) {
  return useQuery({
    queryKey: ["workflows", tenantId],
    queryFn: () => listWorkflows(tenantId),
  });
}

export function useCreateWorkflow(tenantId: string = DEFAULT_TENANT) {
  const client = useQueryClient();
  return useMutation({
    mutationFn: (payload: Omit<CreateWorkflowDTO, "tenantId">) =>
      createWorkflow({ tenantId, ...payload }),
    onSuccess: () => {
      client.invalidateQueries({ queryKey: ["workflows", tenantId] });
    },
  });
}

